import React, {Component} from 'react';
import logo from './brand.png';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'font-awesome/css/font-awesome.min.css';
import './App.css';
import {Welcome, ThankYou} from './Welcome.js';
import {SecondStep, Rate} from './SecondStep.js';
import ThirdStep from './ThirdStep';
import FirstStep from './PersonalInfoForm.js';
import {WiFi,EffectiveTransportation,Accissibility,Bicycle,StreetFurniture,ShoppingMall,ChashLess} from './LastPageParts';


import {BrowserRouter as Router, Route, Switch,Link} from 'react-router-dom';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state={
      user:{}
    }
  }
  update=(e)=>{
    // this.setState((previous,e)=>{
    //   previous:Object.assign(previous,e)
    // })
    console.log(e);
    this.setState(Object.assign(this.state.user,e));
  }
  render() {
    return (
      <div >
        <nav className="navbar navbar-light ">
        <Router>
          <Link className="navbar-brand" to="/"><img src={logo}/></Link></Router>
          <div className="d-flex">
            <a className="ml-2 mr-2" href="#">
              <i className="fa fa-arrow-right"></i>
            </a>
            <a className="ml-2 mr-2" href="#">
              <i className="fa fa-whatsapp"></i>
            </a>
            <a className="ml-2 mr-2" href="#">
              <i className="fa fa-facebook-f"></i>
            </a>
            <a className="ml-2 mr-2" href="#">
              <i className="fa fa-twitter"></i>
            </a>
          </div>
        </nav>
        <Router>
          <div>
            <Route  path="/steps/:step([1])" render={(props)=><FirstStep  {...props} parentFn={this.update} user={this.state.user}/>}/>
            <Route  path="/steps/:step([2])" render={(props)=><SecondStep  {...props} parentFn={this.update} user={this.state.user}/>}/>            
            {/* <Route exact path="/first/second/third/:param"
               render={(match)=><ThirdStep parentFn={this.update} user={this.state.user} 
               item={
                match.params.param=='11'?<WiFi user={this.state.user}/>:<EffectiveTransportation user={this.state.user}/>
               
               }/>}  /> */}

               {/* <Route exact path="/first/second/third/:step" render={(props)=><ThirdStep {...props} user={this.state.user} parentFn={this.update}/>}/> */}
               
          <Route  path="/steps/:step([3-9])"
               render={(props)=><ThirdStep {...props} parentFn={this.update} user={this.state.user} 
               item={
                 props.match.params.step=='3'?<WiFi user={this.state.user}/>:
                 props.match.params.step=='4'?<EffectiveTransportation user={this.state.user}/>:
                 props.match.params.step=='5'?<Accissibility user={this.state.user}/>:
                 props.match.params.step=='6'?<Bicycle user={this.state.user}/>:
                 props.match.params.step=='7'?<StreetFurniture user={this.state.user}/>:
                 props.match.params.step=='8'?<ShoppingMall user={this.state.user}/>:
                 props.match.params.step=='9'?<ChashLess user={this.state.user}/>:''
                 
                }/>}  />
                <Route exact path="/steps/10" component={ThankYou} />

            <Route exact path="/" component={Welcome}/>
          </div>
        </Router>
      </div>
    );
  }
}
export default App;



