import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {Rate} from './SecondStep';
import {Welcome} from './Welcome';
import Progress from './Progress';


class WiFi extends React.Component{
    constructor(props) {
    super(props);
    this.state = {
      user: {
        wifiCoverage:props.user.wifiCoverage|| 3,
        wifiSpeed:props.user.wifiSpeed|| 3,
        transportaionRoute:props.user.transportaionRoute|| 3,
        transportationEffeciency:props.user.transportationEffeciency|| 3,
        accessibilityToCyber:props.user.accessibilityToCyber|| 3,
        parking:props.user.parking|| 3,
        bicycleLanes:props.user.bicycleLanes|| 3,
        bicycleParking:props.user.bicycleParking|| 3,
        busShelter:props.user.busShelter|| 3,
        streelLighting: props.user.streelLighting|| 3,
        trafficSignages:props.user.trafficSignages|| 3,
        pedestrianWalkway:props.user.pedestrianWalkway|| 3,
        shoppingMall:props.user.shoppingMall|| 3,
        safety:props.user.safety|| 3,
        cashBySmartphone:props.user.cashBySmartphone||"true",
        preferCashless:props.user.preferCashless||"true"
       }
    }
  }
  _handleChange = (e) => {
    var tmp = this.state.user;
    tmp[e.target.name] = e.target.value;
    this.setState({user: tmp});
  }
    render (){
        console.log(this.props)
        return (
            [
            <div className="row m-0  col-sm-12 col-xl-7 col-lg-9  col col-12 mx-auto p-0">
            <h5 className="col-12 col-xl-7 col-lg-7">Wi-Fi Spots Services</h5>
            <h6 className="col-12 col-xl-7 col-lg-7 col-sm-12">a. Coverage</h6>
            {[1, 2, 3, 4, 5]
                .map(function (element) {
                return (
                    <label className={'col value_'+element} key={element}>
                    <input
                        type="radio"
                        value={element}
                        name="wifiCoverage"
                        checked={this.state.user.wifiCoverage == element}
                        onChange={this._handleChange}/>
                    <span className="secondCheckMark"></span>
                    </label>
                )
                }, this)
                }
            <input type="text"  placeholder="Remarks:" className="col-12 col-sm-12 col-md-7 col-xl-7 col-lg-7 remark"/>
            </div>,
            <div className="row m-0  col-sm-12 col-xl-7 col-lg-9  col col-12 mx-auto p-0">
            <h6 className="col-12 col-xl-7 col-lg-7 col-sm-12">b. Speed</h6>
            {[1, 2, 3, 4, 5]
                .map(function (element) {
                return (
                    <label className={'col value_'+element} key={element}>
                    <input
                        type="radio"
                        value={element}
                        name="wifiSpeed"
                        checked={this.state.user.wifiSpeed == element}
                        onChange={this._handleChange}/>
                    <span className="secondCheckMark"></span>
                    </label>
                )
                }, this)}
            </div>
            ]
        )
    }
}


