class ChashLess extends Component{
    constructor(props) {
    super(props);
    this.state = {
      user: {
        wifiCoverage:props.user.wifiCoverage|| 3,
        wifiSpeed:props.user.wifiSpeed|| 3,
        transportaionRoute:props.user.transportaionRoute|| 3,
        transportationEffeciency:props.user.transportationEffeciency|| 3,
        accessibilityToCyber:props.user.accessibilityToCyber|| 3,
        parking:props.user.parking|| 3,
        bicycleLanes:props.user.bicycleLanes|| 3,
        bicycleParking:props.user.bicycleParking|| 3,
        busShelter:props.user.busShelter|| 3,
        streelLighting: props.user.streelLighting|| 3,
        trafficSignages:props.user.trafficSignages|| 3,
        pedestrianWalkway:props.user.pedestrianWalkway|| 3,
        shoppingMall:props.user.shoppingMall|| 3,
        safety:props.user.safety|| 3,
        cashBySmartphone:props.user.cashBySmartphone||"true",
        preferCashless:props.user.preferCashless||"true"
       }
    }
  }
  _handleChange = (e) => {
    var tmp = this.state.user;
    tmp[e.target.name] = e.target.value;
    this.setState({user: tmp});
  }
    render=()=>{
        return ([
            <div className="row m-0  col-sm-12 col-xl-7 col-lg-9  col col-12 mx-auto p-0">
            <p className="col-12" id="answerQuestions">Please answer the following questions by ticking the boxes with either yes or no</p>
            <h5 className="col-12 col-xl-7 col-lg-7">Cashing</h5>
              <div className="col-7"></div>
              <h5 className="col-12 col-xl-8 col-lg-8">a. Do you think cashless payment using smartphone
                will make your life more convenient in Cyberjaya?</h5>
    
              <label className="col ml-4">
                <input
                  type="radio"
                  name="cashBySmartphone"
                  value="true"
                  checked={this.state.user.cashBySmartphone=="true"}
                  onChange={this._handleChange}/>Yes
              <span className="secondCheckMark"></span>
              </label>
              <label className="col">
                <input
                  type="radio"
                  name="cashBySmartphone"
                  value="false"
                  checked={this.state.user.cashBySmartphone=="false"}
                  onChange={this._handleChange}/>No
              <span className="secondCheckMark"></span>
              </label>
    
            </div>,
            <div className="row m-0  col-sm-12 col-xl-7 col-lg-9  col col-12 mx-auto p-0">
              <div className="col-7"></div>
              <h5 className="col-12 col-xl-8 col-lg-8">b. Are you willing to use cashless payment using smartphone instead of using cash in Cyberjaya?</h5>
    
              <label className="col ml-4">
                <input
                  type="radio"
                  name="preferCashless"
                  value="true"
                  checked={this.state.user.preferCashless=="true"}
                  onChange={this._handleChange}/>Yes
              <span className="secondCheckMark"></span>
              </label>
              <label className="col">
                <input
                  type="radio"
                  name="preferCashless"
                  value="false"
                  checked={this.state.user.preferCashless=="false"}
                  onChange={this._handleChange}/>No
              <span className="secondCheckMark"></span>
              </label>
    
            </div>
        ])
    }
}

export default class ThirdStep extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user: {
        wifiCoverage:props.user.wifiCoverage|| 3,
        wifiSpeed:props.user.wifiSpeed|| 3,
        transportaionRoute:props.user.transportaionRoute|| 3,
        transportationEffeciency:props.user.transportationEffeciency|| 3,
        accessibilityToCyber:props.user.accessibilityToCyber|| 3,
        parking:props.user.parking|| 3,
        bicycleLanes:props.user.bicycleLanes|| 3,
        bicycleParking:props.user.bicycleParking|| 3,
        busShelter:props.user.busShelter|| 3,
        streelLighting: props.user.streelLighting|| 3,
        trafficSignages:props.user.trafficSignages|| 3,
        pedestrianWalkway:props.user.pedestrianWalkway|| 3,
        shoppingMall:props.user.shoppingMall|| 3,
        safety:props.user.safety|| 3,
        cashBySmartphone:props.user.cashBySmartphone||"true",
        preferCashless:props.user.preferCashless||"true"
       }
    }
  }
  _handleChange = (e) => {
    var tmp = this.state.user;
    tmp[e.target.name] = e.target.value;
    this.setState({user: tmp});
  }
  
}
export {WiFi,EffectiveTransportation,Accissibility,Bicycle,StreetFurniture,ShoppingMall,ChashLess}


