import React from 'react';
export default class Progress extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return (
            <div className={'row m-0 justify-content-center '+ this.props.classes} id="progressBar">
                    <span className="text-uppercase text-default d-sm-block d-none">Progress</span>
                <div className="col-xl-4 col-md-6 col-sm-8 col-12">
                    <span></span>

                    <hr/>

                </div>
            </div>
        )
    }
}


