import React, {Component} from 'react';
import cyber from './images/cyberjaya.png';
import brand from './images/brand.png';
import goToCyber from './images/goToCyber.png';
import {Link} from 'react-router-dom';
class Welcome extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (

      <div className={'firstStep ' + this.props.classes}>

        <div className="row m-0 d-flex  justify-content-center align-items-center">
          <div className="col-sm-6 col-12 col-md-6">
            <img src={cyber} className="img-fluid mb-sm-3" id="cyberJayaImg"/>
          </div>
          <div className="col-sm-auto col-12 p-0">
            <p className="thankingPhrase text-sm-left text-center">Thank you for
              participating in our survey to improve Cyberjaya and make it a more liveable
              <br/>
              city for everyone.
              <br/><br/>Your thoughts regarding the current services and amenities provided
                here will assist us in
              <br/>improving the state of your city further. Rest assured as all responses
                will strictly be kept
              <br/>
              confidential. Together towards a better Cyberjaya for work, play and living!</p>
            <div className="brandInfo">
              <img src={brand}/>
              <p className="d-inline-block">
                3 Lucky Winners will stand a<br/>
                chance to bring home RM300<br/>
                Harvey Norman vouchers monthly!</p>
            </div>
          </div>
          <div className="w-100"></div>
          {
            !this.props.classes &&
          
          <Link
            to="/steps/1"
            className="text-uppercase btn col-11 col-sm-2 mt-5 startNow">Start Now</Link>}
        </div>
      </div>
    )
  }
}

const ThankYou = function () {
  return {
    render() {
      return (
        <div className="lastStep  text-center">
          <div className="row m-0 d-flex  justify-content-center">
            <div className="col-sm-auto col-12">
              <h2>100%</h2>
              <h3>Thank You for
                <br/>
                Completing Our Survey!</h3>
              <p>This survey will be used to improve
                <br/>and enhance the livability of Cyberjaya.</p>
              <h4>Have a nice day</h4>
              <img src={goToCyber}/>
            </div>
            <div className="col-sm-auto col-12"></div>
          </div>
        </div>
      )
    }
  }
}
export {Welcome, ThankYou};


